body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont ,'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "QuickDog";
  src: local("QuickDog."),
    url("./app/assets/fonts/QuickDog/QuickDog.ttf") format("truetype");
  font-weight: bold;
}

@font-face {
  font-family: "TropicalJungle";
  src: local("TropicalJungle."),
    url("./app/assets/fonts/TropicalJungle/TropicalJungle.ttf") format("truetype");
  font-weight: bold;
}